import { OnInit, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { take } from 'rxjs/operators';
import isEqual from 'lodash/isEqual';
import { InputFieldHelper } from '../../../shared/helpers/input-field-helper';
import { ItemService } from '../../../shared/store/ngrx-data/item.service';
import { LanguageInstanceService } from '../../../shared/store/ngrx-data/language-instance.service';
import { LinkToOtherLanguageComponent } from '../link-to-other-language/link-to-other-language.component';
import { LinkToOtherLanguageData } from '../../../shared/models/eav/link-to-other-language-data';
import { LocalizationHelper } from '../../../shared/helpers/localization-helper';
import { TranslationLinkTypeConstants } from '../../../shared/constants/type-constants';
import { ValidationHelper } from '../../validators/validation-helper';
import { TranslateGroupMenuHelpers } from './translate-group-menu.helpers';
import { InputTypeService } from '../../../shared/store/ngrx-data/input-type.service';
import { ContentTypeService } from '../../../shared/store/ngrx-data/content-type.service';
var TranslateGroupMenuComponent = /** @class */ (function () {
    function TranslateGroupMenuComponent(dialog, languageInstanceService, itemService, inputTypeService, contentTypeService) {
        this.dialog = dialog;
        this.languageInstanceService = languageInstanceService;
        this.itemService = itemService;
        this.inputTypeService = inputTypeService;
        this.contentTypeService = contentTypeService;
        this.currentLanguage = '';
        this.defaultLanguage = '';
        this.defaultLanguageMissingValue = false;
        this.headerGroupSlotIsEmpty = false;
        this.translationState = new LinkToOtherLanguageData(null, '', '');
        this.subscriptions = [];
    }
    Object.defineProperty(TranslateGroupMenuComponent.prototype, "toggleTranslateField", {
        set: function (value) {
            if (this.currentLanguage !== this.defaultLanguage) {
                if (this.group.controls[this.config.field.name].disabled) {
                    this.translateUnlink(this.config.field.name);
                }
                else {
                    this.linkToDefault(this.config.field.name);
                }
            }
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(TranslateGroupMenuComponent.prototype, "inputDisabled", {
        get: function () {
            return this.group.controls[this.config.field.name].disabled;
        },
        enumerable: true,
        configurable: true
    });
    TranslateGroupMenuComponent.prototype.ngOnInit = function () {
        this.currentLanguage$ = this.languageInstanceService.getCurrentLanguage(this.config.form.formId);
        this.defaultLanguage$ = this.languageInstanceService.getDefaultLanguage(this.config.form.formId);
        this.fieldConfig = this.config.field;
        this.attributes$ = this.itemService.selectAttributesByEntityId(this.config.entity.entityId, this.config.entity.entityGuid);
        this.subscribeToAttributeValues();
        this.subscribeMenuChange();
        // subscribe to language data
        this.subscribeToCurrentLanguageFromStore();
        this.subscribeToDefaultLanguageFromStore();
        this.subscribeToEntityHeaderFromStore();
        this.subscribeToItemFromStore();
        this.subscribeToContentTypeFromStore();
    };
    TranslateGroupMenuComponent.prototype.ngOnDestroy = function () {
        this.subscriptions.forEach(function (subscriber) { return subscriber.unsubscribe(); });
    };
    TranslateGroupMenuComponent.prototype.openLinkToOtherLanguage = function () {
        var _this = this;
        // Open dialog
        var dialogRef = this.dialog.open(LinkToOtherLanguageComponent, {
            panelClass: 'c-link-to-other-language',
            autoFocus: false,
            width: '350px',
            data: new LinkToOtherLanguageData(this.config.form.formId, this.translationState.linkType, this.translationState.language, this.defaultLanguage, this.attributes, this.config.field.name)
        });
        // spm add dialog and subdialog events through a helper
        dialogRef.keydownEvents().subscribe(function (e) {
            // CTRL + S
            if (e.keyCode === 83 && (navigator.platform.match('Mac') ? e.metaKey : e.ctrlKey)) {
                e.preventDefault(); // spm don't open browser default save
            }
        });
        // Close dialog
        dialogRef.afterClosed().subscribe(function (actionResult) {
            if (actionResult) {
                _this.triggerTranslation(actionResult);
            }
        });
    };
    TranslateGroupMenuComponent.prototype.translateUnlink = function (attributeKey) {
        if (!this.isTranslateEnabled(attributeKey)) {
            return;
        }
        this.itemService.removeItemAttributeDimension(this.config.entity.entityId, attributeKey, this.currentLanguage, this.config.entity.entityGuid);
        var defaultValue = LocalizationHelper.getAttributeValueTranslation(this.attributes[attributeKey], this.defaultLanguage, this.defaultLanguage);
        if (defaultValue) {
            var fieldType = InputFieldHelper.getFieldType(this.config, attributeKey);
            this.itemService.addAttributeValue(this.config.entity.entityId, attributeKey, defaultValue.value, this.currentLanguage, false, this.config.entity.entityGuid, fieldType);
        }
        else {
            console.log(this.currentLanguage + ': Cant copy value from ' + this.defaultLanguage + ' because that value does not exist.');
        }
        this.refreshControlConfig(attributeKey);
    };
    TranslateGroupMenuComponent.prototype.linkToDefault = function (attributeKey) {
        if (!this.isTranslateEnabled(attributeKey)) {
            return;
        }
        this.itemService.removeItemAttributeDimension(this.config.entity.entityId, attributeKey, this.currentLanguage, this.config.entity.entityGuid);
        this.refreshControlConfig(attributeKey);
    };
    TranslateGroupMenuComponent.prototype.translateAll = function () {
        var _this = this;
        this.setTranslationState(TranslationLinkTypeConstants.translate, '');
        Object.keys(this.attributes).forEach(function (attributeKey) {
            _this.translateUnlink(attributeKey);
        });
        this.languageInstanceService.triggerLocalizationWrapperMenuChange();
    };
    TranslateGroupMenuComponent.prototype.dontTranslateAll = function () {
        var _this = this;
        this.setTranslationState(TranslationLinkTypeConstants.dontTranslate, '');
        Object.keys(this.attributes).forEach(function (attributeKey) {
            _this.linkToDefault(attributeKey);
        });
        this.languageInstanceService.triggerLocalizationWrapperMenuChange();
    };
    TranslateGroupMenuComponent.prototype.copyFromAll = function (languageKey) {
        var _this = this;
        this.setTranslationState(TranslationLinkTypeConstants.linkCopyFrom, languageKey);
        Object.keys(this.attributes).forEach(function (attributeKey) {
            _this.copyFrom(languageKey, attributeKey);
        });
        this.languageInstanceService.triggerLocalizationWrapperMenuChange();
    };
    /**
     * Copy value where language is copyFromLanguageKey to value where language is current language
     * If value of current language don't exist then add new value
     * @param copyFromLanguageKey
     */
    TranslateGroupMenuComponent.prototype.copyFrom = function (copyFromLanguageKey, attributeKey) {
        if (!this.isTranslateEnabled(attributeKey)) {
            return;
        }
        var attributeValueTranslation = LocalizationHelper.getAttributeValueTranslation(this.attributes[attributeKey], copyFromLanguageKey, this.defaultLanguage);
        if (attributeValueTranslation) {
            var valueAlreadyExist = this.attributes ?
                LocalizationHelper.isEditableOrReadonlyTranslationExist(this.attributes[attributeKey], this.currentLanguage, this.defaultLanguage)
                : false;
            if (valueAlreadyExist) {
                // Copy attribute value where language is languageKey to value where language is current language
                this.itemService.updateItemAttributeValue(this.config.entity.entityId, attributeKey, attributeValueTranslation.value, this.currentLanguage, this.defaultLanguage, false, this.config.entity.entityGuid);
            }
            else {
                // Copy attribute value where language is languageKey to new attribute with current language
                this.itemService.addAttributeValue(this.config.entity.entityId, attributeKey, attributeValueTranslation.value, this.currentLanguage, false, this.config.entity.entityGuid, this.config.field.type);
            }
        }
        else {
            console.log(this.currentLanguage + ': Cant copy value from ' + copyFromLanguageKey + ' because that value does not exist.');
        }
        this.refreshControlConfig(attributeKey);
    };
    TranslateGroupMenuComponent.prototype.linkReadOnlyAll = function (languageKey) {
        var _this = this;
        this.setTranslationState(TranslationLinkTypeConstants.linkReadOnly, languageKey);
        Object.keys(this.attributes).forEach(function (attributeKey) {
            _this.linkReadOnly(languageKey, attributeKey);
        });
        this.languageInstanceService.triggerLocalizationWrapperMenuChange();
    };
    TranslateGroupMenuComponent.prototype.linkReadOnly = function (languageKey, attributeKey) {
        if (!this.isTranslateEnabled(attributeKey)) {
            return;
        }
        this.setTranslationState(TranslationLinkTypeConstants.linkReadOnly, languageKey);
        this.itemService.removeItemAttributeDimension(this.config.entity.entityId, attributeKey, this.currentLanguage, this.config.entity.entityGuid);
        this.itemService.addItemAttributeDimension(this.config.entity.entityId, attributeKey, this.currentLanguage, languageKey, this.defaultLanguage, true, this.config.entity.entityGuid);
        // TODO: investigate can only triger current language change to disable controls ???
        // this.languageService.updateCurrentLanguage(this.currentLanguage);
        this.refreshControlConfig(attributeKey);
    };
    TranslateGroupMenuComponent.prototype.linkReadWriteAll = function (languageKey) {
        var _this = this;
        this.setTranslationState(TranslationLinkTypeConstants.linkReadWrite, languageKey);
        Object.keys(this.attributes).forEach(function (attributeKey) {
            _this.linkReadWrite(languageKey, attributeKey);
        });
        this.languageInstanceService.triggerLocalizationWrapperMenuChange();
    };
    TranslateGroupMenuComponent.prototype.linkReadWrite = function (languageKey, attributeKey) {
        if (!this.isTranslateEnabled(attributeKey)) {
            return;
        }
        this.setTranslationState(TranslationLinkTypeConstants.linkReadWrite, languageKey);
        this.itemService.removeItemAttributeDimension(this.config.entity.entityId, attributeKey, this.currentLanguage, this.config.entity.entityGuid);
        this.itemService.addItemAttributeDimension(this.config.entity.entityId, attributeKey, this.currentLanguage, languageKey, this.defaultLanguage, false, this.config.entity.entityGuid);
        this.refreshControlConfig(attributeKey);
    };
    TranslateGroupMenuComponent.prototype.getTranslationStateClass = function () {
        if (!this.translationState) {
            return '';
        }
        switch (this.translationState.linkType) {
            case TranslationLinkTypeConstants.missingDefaultLangValue:
                return 'eav-localization-missing-default-lang-value';
            case TranslationLinkTypeConstants.translate:
            case TranslationLinkTypeConstants.linkCopyFrom:
                return 'eav-localization-translate';
            case TranslationLinkTypeConstants.dontTranslate:
                return '';
            case TranslationLinkTypeConstants.linkReadOnly:
                return 'eav-localization-link-read-only';
            case TranslationLinkTypeConstants.linkReadWrite:
                return 'eav-localization-link-read-write';
            default:
                return '';
        }
    };
    TranslateGroupMenuComponent.prototype.refreshControlConfig = function (attributeKey) {
        if (!this.fieldConfig.isParentGroup) {
            this.setControlDisable(this.attributes[attributeKey], attributeKey, this.currentLanguage, this.defaultLanguage);
            this.setAdamDisable();
            this.readTranslationState(this.attributes[this.config.field.name], this.currentLanguage, this.defaultLanguage);
            this.setInfoMessage(this.attributes[this.config.field.name], this.currentLanguage, this.defaultLanguage);
        }
    };
    TranslateGroupMenuComponent.prototype.triggerTranslation = function (actionResult) {
        if (!isEqual(this.translationState, actionResult)) {
            // need be sure that we have a language selected when a link option is clicked
            switch (actionResult.linkType) {
                case TranslationLinkTypeConstants.translate:
                    this.fieldConfig.isParentGroup ? this.translateAll() : this.translateUnlink(this.config.field.name);
                    break;
                case TranslationLinkTypeConstants.dontTranslate:
                    this.fieldConfig.isParentGroup ? this.dontTranslateAll() : this.linkToDefault(this.config.field.name);
                    break;
                case TranslationLinkTypeConstants.linkReadOnly:
                    this.fieldConfig.isParentGroup
                        ? this.linkReadOnlyAll(actionResult.language)
                        : this.linkReadOnly(actionResult.language, this.config.field.name);
                    break;
                case TranslationLinkTypeConstants.linkReadWrite:
                    this.fieldConfig.isParentGroup
                        ? this.linkReadWriteAll(actionResult.language)
                        : this.linkReadWrite(actionResult.language, this.config.field.name);
                    break;
                case TranslationLinkTypeConstants.linkCopyFrom:
                    this.fieldConfig.isParentGroup
                        ? this.copyFromAll(actionResult.language)
                        : this.copyFrom(actionResult.language, this.config.field.name);
                    break;
                default:
                    break;
            }
        }
    };
    TranslateGroupMenuComponent.prototype.setTranslationState = function (linkType, language) {
        this.translationState.linkType = linkType;
        this.translationState.language = language;
    };
    /**
   * Determine is control disabled or enabled
   * @param attributes
   * @param attributeKey
   * @param currentLanguage
   * @param defaultLanguage
   */
    TranslateGroupMenuComponent.prototype.setControlDisable = function (attributes, attributeKey, currentLanguage, defaultLanguage) {
        // bitno
        // if control already disabled through settings then skip
        if (!this.config.field.disabled) {
            // if header group slot is empty disable control
            if (this.headerGroupSlotIsEmpty) {
                this.group.controls[attributeKey].disable({ emitEvent: false });
            }
            else if (currentLanguage === defaultLanguage) {
                this.group.controls[attributeKey].enable({ emitEvent: false });
            }
            else { // else set enable/disable depending on editable translation exist
                if (!LocalizationHelper.translationExistsInDefault(attributes, defaultLanguage)) {
                    this.group.controls[attributeKey].disable({ emitEvent: false });
                    this.defaultLanguageMissingValue = true;
                }
                else {
                    this.defaultLanguageMissingValue = false;
                    if (LocalizationHelper.isEditableTranslationExist(attributes, currentLanguage, defaultLanguage)) {
                        this.group.controls[attributeKey].enable({ emitEvent: false });
                    }
                    else if (LocalizationHelper.isReadonlyTranslationExist(attributes, currentLanguage)) {
                        this.group.controls[attributeKey].disable({ emitEvent: false });
                    }
                    else {
                        this.group.controls[attributeKey].disable({ emitEvent: false });
                    }
                }
            }
        }
    };
    /**
     * translate a field configuration (labels, validation, )
     * @param currentLanguage
     */
    TranslateGroupMenuComponent.prototype.translateAllConfiguration = function (currentLanguage) {
        this.config.field.settings = LocalizationHelper.translateSettings(this.config.field.fullSettings, this.currentLanguage, this.defaultLanguage);
        this.config.field.label = this.config.field.settings.Name || null;
        this.config.field.validation = ValidationHelper.getValidations(this.config.field.settings);
        this.config.field.required = ValidationHelper.isRequired(this.config.field.settings);
    };
    TranslateGroupMenuComponent.prototype.subscribeToCurrentLanguageFromStore = function () {
        var _this = this;
        this.subscriptions.push(this.currentLanguage$.subscribe(function (currentLanguage) {
            _this.currentLanguage = currentLanguage;
            _this.translateAllConfiguration(_this.currentLanguage);
            _this.refreshControlConfig(_this.config.field.name);
        }));
    };
    TranslateGroupMenuComponent.prototype.subscribeToDefaultLanguageFromStore = function () {
        var _this = this;
        this.subscriptions.push(this.defaultLanguage$.subscribe(function (defaultLanguage) {
            _this.defaultLanguage = defaultLanguage;
            _this.translateAllConfiguration(_this.currentLanguage);
            _this.refreshControlConfig(_this.config.field.name);
        }));
    };
    /**
   * Subscribe to item attribute values
   */
    TranslateGroupMenuComponent.prototype.subscribeToAttributeValues = function () {
        var _this = this;
        this.subscriptions.push(this.attributes$.subscribe(function (attributes) {
            _this.attributes = attributes;
        }));
    };
    TranslateGroupMenuComponent.prototype.subscribeToEntityHeaderFromStore = function () {
        var _this = this;
        if (this.config.entity.header.group && this.config.entity.header.group.slotCanBeEmpty) {
            this.subscriptions.push(this.itemService.selectHeaderByEntityId(this.config.entity.entityId, this.config.entity.entityGuid).subscribe(function (header) {
                if (header.group && !_this.fieldConfig.isParentGroup) {
                    _this.headerGroupSlotIsEmpty = header.group.slotIsEmpty;
                    _this.setControlDisable(_this.attributes[_this.config.field.name], _this.config.field.name, _this.currentLanguage, _this.defaultLanguage);
                }
            }));
        }
    };
    /**
     * Fetch current item
     */
    TranslateGroupMenuComponent.prototype.subscribeToItemFromStore = function () {
        var _this = this;
        this.subscriptions.push(this.itemService.selectItemById(this.config.entity.entityId).subscribe(function (item) {
            _this.item = item;
        }));
    };
    /**
     * Fetch contentType of current item
     */
    TranslateGroupMenuComponent.prototype.subscribeToContentTypeFromStore = function () {
        var _this = this;
        var contentTypeId = this.item.entity.type === null
            ? this.item.header.contentTypeName
            : this.item.entity.type.id;
        this.subscriptions.push(this.contentTypeService.getContentTypeById(contentTypeId).subscribe(function (contentType) {
            _this.contentType = contentType;
        }));
    };
    /**
     * Fetch inputType definition to check if input field of this type shouldn't be translated
     * @param attributeType new attribute type defined in contentTypes
     */
    TranslateGroupMenuComponent.prototype.isTranslateEnabled = function (attributeKey) {
        if (!LocalizationHelper.translationExistsInDefault(this.attributes[attributeKey], this.defaultLanguage)) {
            return false;
        }
        var attributeDef = this.contentType.contentType.attributes.find(function (attr) { return attr.name === attributeKey; });
        var calculatedInputType = InputFieldHelper.calculateInputType(attributeDef, this.inputTypeService);
        var inputType;
        this.inputTypeService.getInputTypeById(calculatedInputType.inputType).pipe(take(1)).subscribe(function (type) { inputType = type; });
        if (!inputType) {
            // if you dont find it assume its translateable
            return true;
        }
        return !inputType.DisableI18n;
    };
    TranslateGroupMenuComponent.prototype.readTranslationState = function (attributes, currentLanguage, defaultLanguage) {
        // Determine is control disabled or enabled and info message
        if (!LocalizationHelper.translationExistsInDefault(attributes, defaultLanguage)) {
            this.setTranslationState(TranslationLinkTypeConstants.missingDefaultLangValue, '');
        }
        else if (LocalizationHelper.isEditableTranslationExist(attributes, currentLanguage, defaultLanguage)) {
            var editableElements = LocalizationHelper.getAttributeValueTranslation(attributes, currentLanguage, defaultLanguage)
                .dimensions.filter(function (f) { return f.value !== currentLanguage; });
            if (editableElements.length > 0) {
                this.setTranslationState(TranslationLinkTypeConstants.linkReadWrite, editableElements[0].value);
            }
            else {
                this.setTranslationState(TranslationLinkTypeConstants.translate, '');
            }
        }
        else if (LocalizationHelper.isReadonlyTranslationExist(attributes, currentLanguage)) {
            var readOnlyElements = LocalizationHelper.getAttributeValueTranslation(attributes, currentLanguage, defaultLanguage)
                .dimensions.filter(function (f) { return f.value !== currentLanguage; });
            this.setTranslationState(TranslationLinkTypeConstants.linkReadOnly, readOnlyElements[0].value);
        }
        else {
            this.setTranslationState(TranslationLinkTypeConstants.dontTranslate, '');
        }
    };
    /**
      * Subscribe triggered when changing all in menu (forAllFields)
      */
    TranslateGroupMenuComponent.prototype.subscribeMenuChange = function () {
        var _this = this;
        this.subscriptions.push(this.languageInstanceService.localizationWrapperMenuChange$.subscribe(function (s) {
            if (!_this.fieldConfig.isParentGroup) {
                _this.refreshControlConfig(_this.config.field.name);
            }
        }));
    };
    /**
     * * Change adam disable state
     *   * @param attributeKey
     *  */
    TranslateGroupMenuComponent.prototype.setAdamDisable = function () {
        // set Adam disabled state
        if (this.config.adam) {
            this.config.adam.disabled = this.group.controls[this.config.field.name].disabled;
        }
    };
    /**
     * set info message
     * @param attributes
     * @param currentLanguage
     * @param defaultLanguage
     */
    TranslateGroupMenuComponent.prototype.setInfoMessage = function (attributes, currentLanguage, defaultLanguage) {
        // Determine whether control is disabled or enabled and info message
        if (this.fieldConfig.disableI18n) {
            this.infoMessage = '';
            this.infoMessageLabel = 'LangMenu.InAllLanguages';
            return;
        }
        else if (!LocalizationHelper.translationExistsInDefault(attributes, defaultLanguage)) {
            this.infoMessage = defaultLanguage;
            this.infoMessageLabel = 'LangMenu.MissingDefaultLangValue';
            return;
        }
        var isEditableTranslationExist = LocalizationHelper.isEditableTranslationExist(attributes, currentLanguage, defaultLanguage);
        var isReadonlyTranslationExist = LocalizationHelper.isReadonlyTranslationExist(attributes, currentLanguage);
        if (isEditableTranslationExist || isReadonlyTranslationExist) {
            var dimensions = LocalizationHelper.getAttributeValueTranslation(attributes, currentLanguage, defaultLanguage)
                .dimensions.map(function (d) { return d.value; });
            dimensions = dimensions.filter(function (d) { return !d.includes(currentLanguage); });
            var isShared = dimensions.length > 0;
            if (isShared) {
                this.infoMessage = TranslateGroupMenuHelpers.calculateSharedInfoMessage(dimensions, currentLanguage);
                if (isEditableTranslationExist) {
                    this.infoMessageLabel = 'LangMenu.In';
                }
                else if (isReadonlyTranslationExist) {
                    this.infoMessageLabel = 'LangMenu.From';
                }
            }
            else {
                this.infoMessage = '';
                this.infoMessageLabel = '';
            }
        }
        else {
            this.infoMessage = '';
            this.infoMessageLabel = 'LangMenu.UseDefault';
        }
    };
    return TranslateGroupMenuComponent;
}());
export { TranslateGroupMenuComponent };
